@font-face {
    font-family: "Iran-Yekan";
    src: url(./fonts/IRANYekanBoldFaNum.ttf);
}

@font-face {
    font-family: "Iran-Yekan-ExtraBold";
    src: url(./fonts/IRANYekanExtraBoldFaNum.ttf);
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline-style: none;
    border: none;
    font-family: "Iran-Yekan";
    user-select: none;
    user-zoom: fixed;
}

